import React from "react";
import styled from "styled-components";
import Navbar from "./Navbar";
const Section = styled.div`
  height: 100vh;
  color: white;
  scroll-snap-align: start;
  display: flex;
  flex-direction: column;
`;
const Container = styled.div`
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
`;
const Front = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
`;
const Back = styled.div``;
const Title = styled.div`
  text-align: center;
  font-size: 10rem;
  font-weight: bold;
  line-height: 0.9em;
`;
const Desc = styled.div`
  text-align: center;
  font-size: 1.5rem;
`;

const Button = styled.button`
  display: block;
  width: 150px;
  font-size: 1.2rem;
  padding: 20px;
  cursor: pointer;
  text-align: center;
  outline: none;
  color: white;
  background-color: rebeccapurple;
  border: none;
  border-radius: 40px;
  box-shadow: 0 4px #999;
  &: hover {
    background-color: #7e3fbd;
  }

  &: active {
    background-color: #7e3fbd;
    box-shadow: 0 2px #666;
    transform: translateY(4px);
  }
`;

const HERO = () => {
  return (
    <Section>
      <Navbar />
      <Container>
        <Front>
          <Title>
            DESIGNING <br /> EVOLUTION
          </Title>
          <br />
          <Desc>
            Eager to leverage Web2 experience while delving deeper into the
            world of Web3
            <br /> creating impactful solutions in <b> DeFi</b>, <b>DAO</b> and{" "}
            <b>Web3</b>-oriented spaces.
          </Desc>
          <Button>Contact Me</Button>
        </Front>
        <Back></Back>
      </Container>
    </Section>
  );
};

export default HERO;
